import React from 'react';
import { nanoid } from 'nanoid';
import { useNavigate } from 'react-router-dom';
import { Button, Grid, Box } from '@mui/material';
import useSelectedTableRow from './hooks';
import { Table } from '@/components';
import TableToolbar from '@/components/table/tableToolbar';
import FilterListIcon from '@mui/icons-material/FilterList';
import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';

function createData(iin, name) {
    return {
        id: nanoid(8),
        iin,
        name
    };
}

const rows = [
    createData('890230151025', 'Зимина Любовь Юрьевна'),
    createData('900230151025', 'Аксенова Надежда Валерьевна'),
    createData('890560151025', 'Семенов Дмитрий Кириллович'),
    createData('920230151025', 'Фомина Ирина Юрьевна'),
    createData('890230151025', 'Иванов Сергей Петрович'),
    createData('920230151030', 'Семенов Иван Кириллович'),
    createData('920232551025', 'Ким Аркадий Константинович'),
    createData('945230151025', 'Ли Александра Сергеевна'),
    createData('000230151025', 'Корнилова Валерия Николлаевна'),
    createData('560890151025', 'Капустин Михаил Семенович'),
    createData('920230151895', 'Пушкин Александр Сергеевич'),
    createData('984524562663', 'Печкин Александр Александрович'),
    createData('952636654555', 'Косяков Алексей Сергеевич')
];

const headCells = [
    {
        id: 'iin',
        numeric: false,
        disablePadding: false,
        label: 'ИИН / БИН'
    },
    {
        id: 'name',
        numeric: false,
        disablePadding: false,
        label: 'Ф.И.О / Наименование'
    }
];

function ClientList() {
    const navigate = useNavigate();
    const [selected, handleSelectAllTableRows, handleSelectTableRow] = useSelectedTableRow([], rows);
    const handleNavigate = () => navigate('/clients/create');
    return (
        <>
            <Box gridColumn="span 12" sx={{ display: 'flex', justifyContent: 'right' }}>
                <Button
                    variant="contained"
                    startIcon={<CreateNewFolderIcon />}
                    sx={{ background: '#0064ff', mr: 3 }}
                    onClick={handleNavigate}
                >
                    <span style={{ fontWeight: 'bold' }}>Создать</span>
                </Button>
            </Box>
            <Box gridColumn="span 12">
                <Table
                    headCells={headCells}
                    rows={rows}
                    selected={selected}
                    handleSelectAllTableRows={handleSelectAllTableRows}
                    handleSelectTableRow={handleSelectTableRow}
                >
                    <TableToolbar numSelected={selected.length}>
                        <Button variant="contained" startIcon={<FilterListIcon />}>
                            Фильтр
                        </Button>
                    </TableToolbar>
                </Table>
            </Box>
        </>
    );
}

export default ClientList;
