import React, { useState } from 'react';

const useSelectedTableRow = (arr = [], rows) => {
    const [selected, setSelected] = useState(arr);

    const handleSelectAllTableRows = (event) => {
        if (event.target.checked) {
            const newSelecteds = rows.map((n) => n.id);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleSelectTableRow = (event, id) => {
        const selectedId = selected.indexOf(id);
        let newSelected = [];

        switch (selectedId) {
            case -1:
                newSelected = [...selected, id];
                break;
            case 0:
                newSelected = [...selected.slice(1)];
                break;
            case selected.length - 1:
                newSelected = [...selected.slice(0, -1)];
                break;
            default:
                newSelected = [...selected.slice(0, selectedId), ...selected.slice(selectedId + 1)];
        }

        setSelected(newSelected);
    };

    return [selected, handleSelectAllTableRows, handleSelectTableRow];
};

export default useSelectedTableRow;
